import React from "react";
import { Link } from "gatsby";
import me from "./../../images/me.jpg"

const skills = [
    "Java",
    "C# with ADO.Net and .Net Framework",
    "Javacript with libraries including ReactJS and AngularJS",
    "PHP, CSS and HTML",
    "Mobile application development using Android Studio",
    "Database stuff including MySQL, SQLite and writing SQL.",
    "Embedded Systems Development in C and C++.",
    "Git"
];

const About = function (){
    return (
        <section id="about">
            <h2>About Me</h2>
            <div id="about-inner">
                <div>
                    <p>Hello, I am Chris. I am a new graduate from Northumbria University looking for a role in software development.</p>
                    <p>I studied Computer Science at Northumbria obtaining a First class MComP Degree with Honours. Many different modules were covered such as software development and designs with UML, games programming and embedded system programming with theories, so i am pretty flexible.</p>
                    <p>My best skills revolve around web development and general software engineering. Skills i have best include:</p>
                    <ul id="skills">
                        {skills && skills.map((skill, i) => <li key={i}>{skill}</li>)}
                    </ul>
                    <p>I'm currently looking for a position. Feel free to contact me if you have something suitable or just to ask questions.</p>
                    <Link to="#contact" id="about-contact-button">Contact</Link>
                </div>
  
                <img src={me} alt="Me" id="aboutFigure"/>
            </div>
        </section>
    );
};


export default About;